body{
  overflow-x:hidden;
}
.header{
  width: 100%;
  height: auto;
}
.banner{
  width: 100%;
  height: 24px;
  line-height: 24px;
  background-color: black;
  display: inline-block;
  position: relative;
}
.banner p,
.banner .close{
  position: absolute;
  color: white;
}
.banner p{
  font-size: 12px;
  left: 50%;
  transform: translatex(-50%);
}
.banner .close{
  cursor: pointer;
  right: 2%;
}
.navbar_wrap{
  width: 100%;
  height: 50px;
  padding-top: 25px;
  display: flex;
  position: fixed;
  z-index: 5;
  justify-content: space-between;
}

.left_nav{
  display: flex;
}
.navbar_wrap #category{
  width: 70px;
  font-size: 2.2rem;
}

.navbar_wrap #category a{
  color: black;
}
.navbar_wrap #category ul li{
  cursor: pointer;
  font-size: 28px;
  padding-top: 10px;
  position: relative;
  /* shop 마우스 오버시 나오게*/
}

.navbar_left{
  display: flex;
  padding-left: 25px;
}
.navbar_wrap #category ul li:hover::before{
  content: '';
  position: absolute;
  width: 60px;
  height: 5px;
  bottom: -10px;
  left: 0;
  background-color: black;
}
.search{
  margin: 5px 16px;
  position: relative;
  font-size: 2.2rem;
  cursor: pointer;
}
.modalContent{
  position: absolute;
  z-index: 999;
  background-color: rgba(255, 255, 255, 0.8);
  width: 100%;
  height: 250px;
}
.modalContent input{
  background-color: transparent;
  border-radius: 0px;
  border-bottom: 3px solid black; 
  font-size: 15px;
  width: 500px;
}
fieldset{
  text-align: center;
  margin-top: 30px;
}
.modalclose{
  width: 100%;
  margin-top: 20px;
  height: 35px;
  position: relative;
}

.modalclose>span{
  cursor: pointer;
  right: 5%;
  position: absolute;
  width: 35px;
  height: 2px;
  background-color: #000;
}


.modalclose>span:nth-child(1){
  top: 43%;
  transform: rotate(-45deg);
}
.modalclose>span:nth-child(2){
  bottom: 50%;
  transform: translateX(-50%);
  transform: rotate(45deg);
}

.logo{
  
}
.logo img{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 60px;
  margin-top: -10px;
}
.navbar_rigth{
  width: 33.333%;
  display: flex;
  justify-content: right;
  padding-right: 25px;
}

.Cart_icon a{
  margin-right: 20px;
  font-size: 2rem;
  color: black;
}
.nav{
  width: 40px;
}
.nav .trigger{
  width: 40px;
  height: 30px;
  position: relative;
  cursor: pointer;
}
.nav .trigger span{
  display: block;
  width: 100%;
  height: 4px;
  background-color: black;
  transition: 0.35s;
}
.nav .trigger span:nth-child(1){
  position: absolute;
  top: 0;
}
.nav .trigger span:nth-child(2){
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.nav .trigger span:nth-child(3){
  position: absolute;
  background-color: black;
  bottom: 0;
}
/* 트리거에 엑티브 추가*/
.active span:nth-child(1){
margin-top: 9px;
transform: rotate(30deg);
}
.active span:nth-child(2){
  display: none;
}
.active span:nth-child(3){
  position: absolute;
  margin-bottom: 16px;
  transform: rotate(-30deg);
}

.nav_link{
  width: 130px;
  display: none;
  margin-top: 5px;

}
.navblock{
  display: block;
}
.nav_link {
  margin-left: -80px !important;

}
.nav_link li{
  text-align: right;
  margin-bottom: 10px;
}
.nav_link li a{
  font-size: 28px;
  color: black;
}
/* MainVisual */
.MainVisual{
  width: 100%;
  height: auto;
}
/* 메인이미지 */
.mySwiper{
  width: 100%;
  height: inherit;
}
.swiper-slide img{
  width: 100%;
}

.main_link{
  width: 100%;
  height: 165px;
  display: flex;
  justify-content: space-between;
}
.main_link a{
  flex: 1;
  text-align: center;
  line-height: 150px;
  font-size: 120px;
  transition: .25s;
  font-weight: bold;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}
.main_link a:hover{
  -webkit-text-fill-color: black;
}
.main_link a:nth-child(2):hover{
  -webkit-text-fill-color: yellow;
}
.main_visual_box{
  width: 100%;
  display: inline-block
}
.main_visual_box a img{
  width: 25%;
  height: auto;
  background-size: cover;
}
.main_item_wrap{
  width: 100%;
  height: auto;
}
.main_item_inner{
  padding: 2.5rem 0;
  width: 65%;
  margin: auto;
}
.main_item{
  display: flex;
  flex-wrap: wrap;
}
.main_item_inner img{
  width: 33%;
}
.MainVisual h4{
  font-size: 90px;
  font-weight: bold;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
  text-align: center;
}
.instagrem_box{
  padding: 2rem;
  display: flex;
  gap: 8px;
  justify-content: center;
}
.instagrem_box img{
  width: 13%;
}
.ProductCard {
  margin-bottom: 20px;
}

.All_item_text,
.Bag_text,
.Cap_text,
.clothes_text{
  font-size: 20px;
  line-height: 30px;
  color: black;
  span{
    font-size: 0.7rem;
    padding: 0.4rem 0.8rem;
    background-color: yellow;
    font-weight: bold;
    
  }
}
.ProductAll_inner,
.bag_inner,
.cap_inner,
.top_inner,
.Info_inner,
.Cart_inner
{
  width: 80%;
  padding: 150px 0;
  margin: auto;
}
/* 공통제목 */
h2{
  padding-bottom: 1.4rem;
  font-size: 7vw;
  font-weight: bold;
  width: 100%;
  text-align: center;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}
.All_item,
.bag_item,
.cap_item,
.top_item
{
